@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,menu,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,main,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block}*[hidden]{display:none}body{line-height:1}menu,ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{color:#697077;font-family:"Raleway", system-ui, sans-serif;background-color:#f2f4f8;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased}main{display:flex;height:100vh;align-items:center;justify-content:center}h1{font-size:5.75rem;font-weight:300;letter-spacing:-.04rem;line-height:6.375rem;text-transform:lowercase;opacity:1;transform:translate3d(0, 0, 0);transition-duration:0.7s;transition-property:opacity, transform;transition-timing-function:cubic-bezier(0, 0, 0.3, 1)}.loading{opacity:0;transform:translate3d(0, 50%, 0)}

